@import "fonts";
@import "base";
@import "vars";

.header {
  border: 1px solid #ececec;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.17);
  &__flex {
    display: flex;
    justify-content: space-between;
    height: 100px;
    align-items: center; }
  &__left {
    display: flex;
    align-items: center; }
  &__form {
    display: flex; }
  &__text {
    width: 281px;
    height: 38px;
    border-radius: 0 3px 3px 0;
    border: 1px solid #e6e6e6;
    background-color: $light;
    padding-left: 16px;
    color: #a4a4a4;
    font-size: 14px;
    font-weight: 300;
    line-height: 32px;
    letter-spacing: 0.35px;
    border-radius: 0 0 0 0; }
  &__right {
    display: flex;
    align-items: flex-end; } }

.logo__text {
  color: $dark;
  font-size: 27px;
  font-family: lato-light;
  text-decoration: none;
  margin-left: 13px;
  margin-right: 37px; }

.other-color {
  color: $accent;
  font-family: lato-black; }

.button__search {
  width: 36px;
  border-radius: 0 3px 3px 0;
  border: 1px solid #e6e6e6;
  background-color: $light;
  border-left-color: transparent;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0; }

.form__browse {
  width: 87px;
  height: 38px;
  border-radius: 3px 0 0 3px;
  border: 1px solid #e6e6e6;
  background-color: #cdcdcc;
  border-right-color: transparent;
  background-image: linear-gradient(to bottom, #f9f9f9 0%, #f5f5f5 100%);
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  padding-top: 2px; }

.summary__search {
  display: block;
  color: $dark;
  font-size: 14px;
  line-height: 32px;
  text-align: center;
  &::-webkit-details-marker {
    display: none; }
  &::after {
    content: 'â–¼';
    padding-left: 0.5em;
    font-size: 9px;
    color: #838383; } }

.button__logo {
  width: 130px;
  height: 38px;
  background-color: $accent;
  border: transparent;
  margin-left: 25px;
  padding-top: 2px;
  letter-spacing: 0.38px;
  color: $light; }

.button__lens {
  width: 16px;
  padding-top: 4px;
  padding-right: 2px; }

.dropdown__menu {
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  background-color: $light;
  width: 262px;
  position: relative;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.17);
  z-index: 1;
  &__heading {
    color: #232323;
    font-family: lato-regular;
    font-size: 14px;
    font-weight: 400;
    line-height: 32px;
    text-transform: uppercase;
    letter-spacing: 0.35px;
    border-bottom: 1px solid #e8e8e8; }
  &__item {
    font-size: 14px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.35px;
    text-decoration: none;
    color: #4f4f4f;
    font-family: lato-regular;
    &:hover {
      color: $accent; } } }

.summary__basket {
  display: block;
  font-size: 14px;
  line-height: 32px;
  text-align: center;
  &::-webkit-details-marker {
    display: none; }
  &::after {
    content: 'â–¼';
    padding-left: 0.5em;
    font-size: 9px;
    color: $light; } }

.menu {
  display: flex;
  flex-direction: row;
  width: 680px;
  height: 51px;
  color: $dark;
  font-size: 14px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  &__list {
    list-style-type: none;
    position: relative;
    &:hover .mega__box {
      display: flex; }
    &__last {
      &:hover .mega__box {
        right: 0;
        left: auto; }
      &:hover .mega__box::before {
        right: 26px;
        left: auto; } } } }
.mega__box {
  display: none;
  border: 1px solid #e8e8e8;
  background-color: $light;
  padding: 20px;
  border-radius: 5px;
  position: absolute;
  margin-top: 5px;
  z-index: 1;
  &:before {
    content: '';
    display: block;
    width: 12px;
    height: 12px;
    background-color: $light;
    position: absolute;
    transform: rotate(45deg);
    top: -7px;
    left: 20px;
    border-left: 1px solid #e8e8e8;
    border-top: 1px solid #e8e8e8; }
  &__heading {
    width: 235px;
    border-bottom: 1px solid #e8e8e8;
    margin-bottom: 8px;
    font-family: lato-regular;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase; }
  &__item {
    display: flex;
    flex-direction: column;
    margin-right: 46px;
    &:last-of-type {
      margin-right: 0; } }
  &__link {
    text-decoration: none;
    color: #646464;
    font-family: lato-regular;
    font-size: 14px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.35px;
    text-transform: capitalize;
    &:hover {
      color: $accent; } } }

.banner {
  background-color: #e8e8e8;
  height: 613px;
  &__block {
    background: url("/build/img/bg_banner.png") no-repeat;
    height: 100%;
    background-size: cover;
    display: flex;
    align-items: center; }
  &__text {
    display: flex;
    flex-direction: column;
    height: 92px;
    border-left: 12px solid $accent;
    justify-content: center; }
  &__brand {
    font-size: 60px;
    letter-spacing: 1.5px;
    color: $dark;
    font-family: lato-black;
    margin-left: 12px;
    line-height: 65px; }
  &__size {
    height: 92px; }
  &__fashion {
    font-size: 40px;
    letter-spacing: 1px;
    color: $dark;
    font-family: lato-black;
    margin-left: 12px;
    &__span {
      color: $accent;
      font-family: lato-black; } } }

.promo {
  margin: 100px auto 100px;
  &__list {
    display: flex;
    justify-content: space-between; }
  &__card {
    position: relative; }
  &__hot {
    position: absolute;
    top: 25px;
    width: 175px;
    height: 82px;
    color: $dark;
    font-family: lato-black;
    font-size: 18px;
    text-align: left;
    letter-spacing: 0.45px;
    background: #fff;
    padding-left: 20px;
    padding-top: 16px; }
  &__formen {
    color: $accent;
    font-size: 30px;
    letter-spacing: 0.75px;
    font-family: lato-black; }
  &__trendy {
    width: 225px;
    top: 45px; }
  &__forwomen {
    width: 162px;
    padding-left: 20px;
    text-transform: uppercase; }
  &__new {
    top: 45px;
    text-transform: uppercase; }
  &__photo2 {
    width: 560px;
    height: 261px;
    margin-top: 20px; }
  &__photo4 {
    margin-top: 20px; } }

.cards {
  &__heading {
    text-align: center;
    color: $dark;
    font-size: 30px;
    line-height: 54px;
    letter-spacing: 0.75px; }
  &__paragraph {
    text-align: center;
    color: #9f9f9f;
    font-size: 14px;
    letter-spacing: 0.35px; }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 65px; }
  &__item {
    width: 263px;
    margin-top: 60px;
    position: relative;
    &:hover {
      box-shadow: 0 5px 8px rgba(0, 0, 0, 0.16);
      background-color: $light; }
    &:hover .cards__img {
      filter: brightness(30%); }
    &:hover .cards__add {
      display: flex; } }
  &__link {
    text-decoration: none; }
  &__img {
    width: 100%; }
  &__price {
    padding: 15px; }
  &__text {
    color: $dark;
    font-size: 13px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 17px;
    &__red {
      color: $accent;
      font-size: 16px;
      font-weight: 700;
      text-transform: uppercase; } }
  &__add {
    position: absolute;
    top: 98px;
    display: none;
    justify-content: center;
    width: 100%;
    &__link {
      border: 1px solid $light;
      padding: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      &:hover {
        box-shadow: 0 0 10px $light; } }
    &__img {}
    &__text {
      color: $light;
      font-family: lato-bold;
      font-size: 13px;
      margin-left: 8px; } } }

.button__product {
  display: flex;
  justify-content: center;
  &__text {
    padding: 15px;
    background-color: $accent;
    border: transparent; }
  &__sum {
    display: block;
    color: $light;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.4px;
    &::-webkit-details-marker {
      display: none; }
    &::after {
      content: '\2192';
      padding-left: 0.5em;
      color: $light; } } }

.bigbanner {
  margin: 100px auto;
  &__item {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background-color: #222224; }
  &__right {
    display: grid;
    align-items: center;
    padding-left: 36px;
    padding-right: 43px;
    padding-top: 48px;
    padding-bottom: 66px;
    &__text {
      padding-left: 15px; }
    &__content {
      display: grid;
      grid-template-columns: 1fr 4fr; }
    &__head {
      padding-bottom: 15px;
      color: #fbfbfb;
      font-size: 20px;
      letter-spacing: 0.5px;
      font-size: lato-bold; }
    &__paragraph {
      color: #fbfbfb;
      font-size: 14px;
      font-weight: 300;
      line-height: 24px;
      letter-spacing: 0.35px;
      font-family: lato-light; } } }

.feedback {
  height: 438px;
  margin-bottom: 120px;
  display: grid;
  position: relative;
  &::before {
    content: '';
    display: grid;
    background: url("/build/img/bg_footer.png") no-repeat;
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    z-index: 1;
    background-size: cover; }
  &__item {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
    position: relative;
    z-index: 2; }
  &__left {
    display: grid;
    grid-template-columns: 1fr auto 31px;
    grid-template-rows: 83px 20px;
    grid-column-gap: 28px;
    padding-top: 130px;
    border-right: 1px solid $light;
    height: 100%; }
  &__grid {
    &__img {
      display: grid;
      grid-column: 1/2; }
    &__text {
      color: #222224;
      font-size: 16px;
      font-style: italic;
      line-height: 26px;
      display: grid;
      grid-column: 2/3;
      grid-row: 1/3; }
    &__sing1 {
      color: $accent;
      display: grid;
      grid-column: 2/3;
      grid-row: 2/3; }
    &__sing2 {
      display: grid;
      grid-column: 2/3;
      grid-row: 3/3;
      color: #222224;
      font-size: 12px;
      font-weight: 300;
      letter-spacing: 0.3px;
      line-height: 14px; } }
  &__form {
    text-align: center;
    &__all {
      font-size: 0; }
    &__mail {
      width: 282px;
      height: 46px;
      background-color: #e1e1e1;
      border: none;
      border-radius: 23px 0 0 23px;
      opacity: 0.67;
      color: #222224;
      font-size: 14px;
      letter-spacing: 0.35px;
      padding-left: 28px; }
    &__button {
      width: 104px;
      height: 46px;
      border-radius: 0 23px 23px 0;
      background-color: $accent;
      border: none;
      color: $light;
      font-size: 14px;
      letter-spacing: 0.35px; }
    &__heading {
      color: #222224;
      font-size: 24px;
      letter-spacing: 0.6px;
      line-height: 41px;
      text-transform: uppercase; }
    &__text {
      color: #222224;
      font-size: 14px;
      padding-bottom: 35px; } } }

.footer {
  &__list {
    display: flex;
    padding-bottom: 90px; }
  &__item {
    display: flex;
    flex-direction: column;
    &__logo {
      display: flex;
      align-items: center; }
    &__text {
      color: #898989;
      font-size: 14px;
      font-weight: 300;
      line-height: 24px;
      letter-spacing: 0.35px;
      padding-top: 30px; } }
  &__heading {
    color: $accent;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    padding-bottom: 35px;
    padding-top: 14px; }
  &__link {
    text-decoration: none;
    color: #898989;
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 42px;
    text-transform: capitalize;
    &:hover {
      color: $accent; } }
  &__list1 {
    display: flex;
    flex-direction: column;
    width: 427px; }
  &__list2 {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-left: 64px; }
  &__list3 {
    display: flex;
    flex-direction: column;
    flex: 1; }
  &__list4 {
    display: flex;
    flex-direction: column;
    flex: 1; }
  &__finish {
    height: 79px;
    background-color: #e6e6e6;
    &__one {
      display: grid;
      height: 100%; }
    &__social {
      display: grid;
      align-items: center;
      grid-template-columns: repeat(2, 1fr);
      &__link {
        &:hover {
          border: 1px solid $accent; } }
      &__left {
        color: #7a7a7a;
        font-size: 16px;
        letter-spacing: 0.4px; }
      &__right {
        display: grid;
        grid-template-columns: repeat(5, max-content);
        justify-content: end;
        grid-gap: 5px; } } } }

.product__banner {
  background-color: #f8f3f4;
  height: 148px;
  &__container {
    height: 100%; }
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    height: 100%;
    &__new {
      display: grid;
      justify-content: start;
      &__link {
        text-decoration: none;
        color: $accent;
        font-family: lato-black;
        font-size: 24px;
        font-weight: 400;
        line-height: 20px;
        text-transform: uppercase; } }
    &__list {
      display: grid;
      grid-template-columns: repeat(3, max-content);
      justify-content: end;
      grid-gap: 2px; }
    &__link {
      text-decoration: none;
      font-size: 14px;
      font-weight: 700;
      line-height: 20px;
      text-transform: uppercase;
      color: #636363;
      &__red {
        color: $accent; } } } }

.product__promo {
  &__content {
    display: grid;
    grid-template-columns: 1fr 4fr; }
  &__menu {
    &__side {
      display: grid;
      grid-template-rows: repeat(3, max-content);
      grid-gap: 18px; }
    &__list {
      list-style-type: none;
      border-left: 5px solid #ef5b70;
      border-bottom: 1px solid #ebebeb;
      &__link {
        text-decoration: none;
        color: #6f6e6e;
        font-size: 14px;
        font-weight: 700;
        line-height: 20px;
        text-transform: uppercase;
        padding: 20px; } } }
  &__price {
    display: grid;
    grid-template-columns: repeat(3, max-content);
    grid-template-rows: repeat(4, max-content);
    justify-content: space-between;
    &__filter {
      display: grid;
      grid-column: 1/4;
      grid-template-columns: repeat(3, 1fr);
      &__trending {
        display: grid;
        grid-template-rows: repeat(3, 1fr); } } } }

.product__bigbanner {
  background-color: #222224;
  height: 341px;
  &__container {
    height: 100%; }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    height: 100%;
    &__new {
      display: grid;
      grid-template-rows: repeat(2, max-content);
      align-self: center;
      justify-items: center;
      grid-template-columns: none; }
    &__text {
      text-align: center;
      padding-left: 0;
      padding-top: 32px;
      width: 310px; }
    &__head {
      display: grid;
      padding-left: 0;
      padding-top: 15px; }
    &__paragraph {
      display: grid;
      padding-left: 0; } } }

@import "media";
